} from 'bootstrap-vue'
import axios from 'axios'
// eslint-disable-next-line no-unused-vars

export default {
  components: {
    BRow,

    BCol,
    BCard,
    BCardText,
    BButton,
    BFormRadio,
  },
  data() {
    return {
      status: '', // 

      isLocked: true, // 

      shiyanwei: 0,
      gongzuowei: 0,
      jiedifen: 0,
      jiedihe: 0,
      duanluhe: 0,
      duanlufen: 0,
      qianmen: 0,
      houmen: 0,

    }
  },
  created() {

  },
  beforeMount() {
    axios.get('http://localhost:9999/switch_one')
      .then(response => {

      })
      .catch(err => {
        console.log(err)
      })
  },
  mounted() {
    setInterval(() => {
      axios.post('http://localhost:9999/getdata')
        .then(response => {
          this.qianmen = response.data.qianmen
          this.houmen = response.data.houmen
          this.duanlufen = response.data.duanlufen
          this.duanluhe = response.data.duanluhe
          this.jiedifen = response.data.jiedifen
          this.jiedihe = response.data.jiedihe
          this.gongzuowei = response.data.gongzuowei
          this.shiyanwei = response.data.shiyanwei

          // this.data = res.data
          console.log(this.data)
        })
        .catch(err => {
          console.log(err)
        })
    }, 3000)
  },
  beforeDestroy() {
    axios.get('http://localhost:9999/switch_zero')
      .then(res => {
        console.log(res)
      })
      .catch(err => {
        console.log(err)
      })
  },

  methods: {
    onupdate() {
      console.log('click on')
      axios.get('http://localhost:9999/switch_one')
        .then(response => {
          console.log('click on1')
          // console.log(response.data)
        })
        .catch(err => {
          console.log(err)
        })
    },
    offupdate() {
      console.log('click off')
      axios.get('http://localhost:9999/switch_zero')
      // eslint-disable-next-line no-unused-vars
        .then(response => {
          console.log('click off1')
          // console.log(response.data)
        })
        .catch(err => {
          console.log(err)
        })
    },

